
html{font-size:16px
}
body{margin:0
}
ul{list-style:none;margin:0;padding:0
}
*{font-family:'Roboto', Arial, SansSerif;font-weight:400
}
.mv-observer:nth-child(odd){background-color:#f2f2f2
}
.mv-ring{display:inline-block;position:relative;width:80px;height:80px
}
.mv-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #666;border-radius:50%;animation:mv-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#666 transparent transparent transparent
}
.mv-ring div:nth-child(1){animation-delay:-0.45s
}
.mv-ring div:nth-child(2){animation-delay:-0.3s
}
.mv-ring div:nth-child(3){animation-delay:-0.15s
}
@keyframes mv-ring{
0%{transform:rotate(0deg)
}
100%{transform:rotate(360deg)
}
}
.mv-header{display:flex;width:100%;min-height:20px;background-color:#00637d;color:#fff;padding:0 10px;position:fixed;top:0;left:0;z-index:2
}
.mv-header .mv-burger,.mv-header .mv-menu{margin-left:auto;margin-right:25px
}
.mv-content{min-height:120vh
}
.mv-content.full-height{min-height:100vh
}
.mv-burger{display:block;width:42px;cursor:pointer
}
.mv-burger>span{display:block;height:8px;background-color:#fff;margin-bottom:6px
}
.mv-burger>span:last-child{margin-bottom:0
}
.mv-menu--list>li{position:relative;display:inline-flex
}
.mv-menu--list>li>a{display:inline-block;flex-direction:column;padding:25px;color:#fff;text-decoration:none;text-transform:uppercase;position:relative
}
.mv-menu--list>li:hover:after{position:absolute;bottom:0;left:0;content:'';display:block;width:100%;height:4px;background-color:#fff
}
.mv-menu--list>li ul{opacity:0;visibility:hidden;transition:opacity 0.2s ease 0.2s
}
.mv-menu--list>li:hover ul{opacity:1;visibility:visible
}
.mv-menu--dropdown{width:200px;position:absolute;z-index:2;top:100%;left:0;padding-bottom:10px;background-color:#00637d
}
.mv-menu--dropdown>li>a{display:flex;padding:10px 25px;color:#fff;text-decoration:none;text-transform:uppercase;background-color:#00637d;transition:background-color 0.2s ease
}
.mv-menu--dropdown>li>a:hover{background-color:#71a6ba
}

h1{opacity:1;color:black
}
h1.active{opacity:1;transform:translateX(0px)
}

.observer {
}
